import React from 'react'
import { Container, Grid, Typography,Button } from '@material-ui/core'
import useStyles from './Styles'
import CartItem from './CartItem/Index'
import {Link} from 'react-router-dom'
let Cart = ({cart,hanldeEmptyCart,handleUpdateCartQty,hanldeRemoveFromCart}) => {
     let classes=useStyles()


    const EmptyCart=()=>(
        <Typography variant='subtitle1'>you have no items in your shopping cart, start adding some </Typography>
    )
      
    const FilledCart=()=>(
        <>
        <Grid container spacing={3}>
            {
                cart.line_items.map((item)=>(
                    <Grid item xs={12} sm={4} key={item.id}>
                         <CartItem item={item} handleUpdateCartQty={handleUpdateCartQty} hanldeRemoveFromCart={hanldeRemoveFromCart} />
                    </Grid>
                ))}
        </Grid>
        <div className={classes.cardDetails}>
        <Typography variant="h4">Subtotal: {cart.subtotal.formatted_with_symbol}</Typography>
            <div >
            <Button className={classes.emptyButton} size="large" type="button" variant="contained" color="secondary" onClick={hanldeEmptyCart}>
                    Empty Cart
                </Button>
                <Button className={classes.checkoutButton} component={Link} to='/checkout'  size="large" type="button" variant="contained" color="primary">
                 Checkout Cart
                </Button>
            </div>

        </div>
        </>
    )
    
     
    if(!cart.line_items) return 'Loading...'
    return (
        <Container>
            <div className={classes.toolbar} />
            <Typography className={classes.title} variant='h3' gutterBottom>
                Your Shopping Cart
            </Typography>
            { 

                !cart.line_items.length ? <EmptyCart/> : <FilledCart/>

            }
        </Container>
    )
}

export default Cart
